const load = async ({ locals }) => {
  const { prisma } = locals;
  const committees = await prisma.committee.findMany({
    include: {
      positions: {
        select: {
          mandates: {
            where: {
              startDate: {
                lte: /* @__PURE__ */ new Date()
              },
              endDate: {
                gte: /* @__PURE__ */ new Date()
              }
            },
            select: {
              memberId: true
            }
          }
        },
        orderBy: {
          name: "asc"
        }
      }
    }
  });
  return {
    committees
  };
};
export {
  load
};
